* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
}

.container {
  text-align: center;
  background-color: #fff;
  border: 1px dashed #025bee;
  border-radius: 10px;
  min-width: 400px;
  min-height: 170px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

input[type="file"] {
  display: none;
}

#picker-label {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #025bee;
  border-radius: 4px;
  width: 150px;
  margin: 0 auto;
  padding: 10px 0;
  display: block;
}

#clear {
  cursor: pointer;
  text-decoration: underline;
  display: block;
}

.progress-tools {
  width: 150px;
  margin: 0 auto;
  display: block;
}

.finish-tools {
  display: block;
}

.hide {
  display: none;
}

.show {
  display: block;
}

#url {
  text-align: center;
  width: 500px;
  margin: 0 auto;
  padding: 10px;
  font-size: 12px;
  display: block;
}

#err {
  text-align: center;
  width: 500px;
  margin: 0 auto;
  padding: 10px;
  display: block;
}

.active {
  border: 2px dashed #025bee;
}

#status {
  margin-left: 10px;
  font-size: 20px;
  display: inline-block;
}

#blink {
  font-size: 10px;
  animation: 2s linear infinite blinker;
  display: inline-block;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

